export default {
	data() {
		return {
			trailerList: [],
			keywords: [],  // 搜索关键字
			page: 1,       // 当前页数
			totalPages: 1  // 总页数
		}
	},
	onLoad() {
		var _this = this;
		
		uni.showLoading({
			mask:true,
			title:"请稍后…"
		})
		uni.showNavigationBarLoading()
		
		//查询猜你喜欢
		uni.request({
			url: _this.serverURL + '/search/list?keyword=&page=&pageSize', 
			method:"POST",
			success: (res) => {
				if(res.data.status == 200){
					_this.trailerList = res.data.data.rows;
				}
			},
			complete: () => {
				uni.hideLoading()
				uni.hideNavigationBarLoading()
			}
		});
	},
	onReachBottom() {
		var _this = this;
		var page = _this.page + 1 
		var keywords = _this.keywords
		var totalPages = _this.totalPages
		if( page > totalPages){ return;}
		
		this.pagedTrailerList(keywords,page,15)
	},
	methods: {
		pagedTrailerList(keywords,page,pageSize){
			var _this = this;
			
			uni.showLoading({
				mask:true,
				title:"请稍后…"
			})
			uni.showNavigationBarLoading()
			
			//查询猜你喜欢
			uni.request({
				url: _this.serverURL
					+ '/search/list?keywords=' + keywords
					+ '&page=' + page
					+ '&pageSize=' + pageSize, 
				method:"POST",
				success: (res) => {
					if(res.data.status == 200){
						var tempList = res.data.data.rows;
						_this.trailerList = _this.trailerList.concat(tempList)
						_this.totalPages =res.data.data.total //总页数
						_this.page = page
					}
				},
				complete: () => {
					uni.hideLoading()
					uni.hideNavigationBarLoading()
				}
			});
		},
		searchMe(e){
			var _this = this
			//获取搜索内容
			var value = e.detail.value
			this.keywords = value
			this.trailerList = []
			this.pagedTrailerList(value,1,15)
		},
		showTrailer(e){
			var trailerId =e.currentTarget.dataset.trailerid;
			uni.navigateTo({
				url:"../movie/movie?trailerId="+trailerId
			})
		}
	}
}
